import mapboxgl from '!mapbox-gl'; // eslint-disable-line import/no-webpack-loader-syntax
import React, { Fragment, useEffect, useRef, useState,useContext } from 'react';
import MapContext from '../contexts/MapContext';

mapboxgl.accessToken = 'pk.eyJ1IjoicGljcGljMTk3OSIsImEiOiJjazkwNno0NTAwYThhM2duNDl0ZmxqN3F6In0.cde4v5Pi0WMLUyHrNd5P-g';
 


const Carte_Class = () => {

    const MapInfo = useContext(MapContext);

    const mapContainer = useRef(null);
    const map = useRef(null);
    const [lng, setLng] = useState(-1.6214694296580212);
    const [lat, setLat] = useState(49.63349446077185);
    const [zoom, setZoom] = useState(12);

    
    useEffect(() => {
        
        if (map.current) return; // initialize map only once

        //Si les coordonnées son null on reinitialise la map
        if(sessionStorage.getItem('latitude_cash') == null && sessionStorage.getItem('longitude_cash') == null && sessionStorage.getItem('zoom_cash') == null)
        {
            sessionStorage.setItem('longitude_cash',-1.6214694296580212);
            sessionStorage.setItem('latitude_cash',49.63349446077185);
            sessionStorage.setItem('zoom_cash',12);
            setLat(sessionStorage.getItem('latitude_cash'));
            setLng(sessionStorage.getItem('longitude_cash'));
            setZoom(sessionStorage.getItem('zoom_cash'));
        }

        //creation de la map
        map.current = new mapboxgl.Map({
            container: mapContainer.current,
            style: 'mapbox://styles/picpic1979/ckoe1ilwl1zbi19lknjci52kf',
            center: [sessionStorage.getItem('longitude_cash'), sessionStorage.getItem('latitude_cash')],
            zoom: sessionStorage.getItem('zoom_cash')
        },

        //store les positions de la map 
        setLat(sessionStorage.getItem('latitude_cash')),
        setLng(sessionStorage.getItem('longitude_cash')),
        setZoom(sessionStorage.getItem('zoom_cash'))
        );
    });
     
    useEffect(() => {

        if (!map.current) return; // wait for map to initialize

        map.current.on('move', () => {

            setLng(map.current.getCenter().lng.toFixed(4));
            sessionStorage.setItem('longitude_cash',lng);
            setLat(map.current.getCenter().lat.toFixed(4));
            sessionStorage.setItem('latitude_cash',lat);
            setZoom(map.current.getZoom().toFixed(2));
            sessionStorage.setItem('zoom_cash',zoom);   

        });

        MapInfo.lng = lng;
        MapInfo.lat = lat;
        MapInfo.zoom = zoom;

        MapInfo.Flyto = () => {    
            console.log("fly to position");
            
            Flyto(15,1);
            
        }
    });


    const Flyto = (lng,lat) => {
        
        map.current.flyTo({
        center: [
            lng,lat
        ],
        zoom: 18,
        essential: true // this animation is considered essential with respect to prefers-reduced-motion
     });
    }
     
    return (
        <div>
           <Fragment>
                <div className="sidebar">
                    Longitude: {lng} | Latitude: {lat} | Zoom: {zoom} 
                    <br></br>
                    <button onClick={() => MapInfo.Flyto()}>Aller à</button> 
                </div>
                <div width="100vw" height="100vh" ref={mapContainer} className="map-container">
                </div>
            </Fragment>
        </div>
    );

};

export default Carte_Class;